export default function PageHeader({ title, description }) {
  return (
    <div className="text-center mb-8">
      <h2 className="text-3xl font-playfair font-bold text-white mb-2">
        {title}
      </h2>
      <p className="text-slate-300 text-lg">
        {description}
      </p>
    </div>
  );
}
